<template>
	<Dashboard>
		<div>
			<div class="mb-4 text-white">
				<h5>Dashboard</h5>
				<small>Dashboard</small>
			</div>
			
			<div class="row">
				<div class="col-md-6">
					<div class="card">
						<div class="card-body">
							<h5 class="mb-4">Izin yang akan kadaluarsa dalam waktu:</h5>
							<hr>
							
							<div class="mb-4 card-body border">
								<h5>12 Bulan</h5>
								<div class="content-count"><span id="permit-12m" v-html="permit12m"></span> izin akan kadaluarsa</div>
							</div>
							
							<div class="mb-4 card-body border">
								<h5>6 Bulan</h5>
								<div class="content-count"><span id="permit-6m" v-html="permit6m"></span> izin akan kadaluarsa</div>
							</div>
							
							<div class="mb-4 card-body border">
								<h5>3 Bulan</h5>
								<div class="content-count"><span id="permit-3m" v-html="permit3m"></span> izin akan kadaluarsa</div>
							</div>
							<div class="mb-4 card-body border">
								<h5>1 Bulan</h5>
								<div class="content-count"><span id="permit-1m" v-html="permit1m"></span> izin akan kadaluarsa</div>
							</div>
							<div class="mb-4 card-body border">
								<h5>1 Minggu</h5>
								<div class="content-count"><span id="permit-1w" v-html="permit1w"></span> izin akan kadaluarsa</div>
							</div>
						</div>
					</div>
				</div>
				<div class="col-md-6">
					<div class="card">
						<div class="card-body">
							<h5 class="mb-4">Pelaporan yang harus dilaporkan dalam waktu:</h5>
							<hr>
							
							<div class="mb-4 card-body border">
								<h5>12 Bulan</h5>
								<div class="content-count"><span id="report-12m" v-html="report12m"></span> pelaporan harus dilaporkan</div>
							</div>
							
							<div class="mb-4 card-body border">
								<h5>6 Bulan</h5>
								<div class="content-count"><span id="report-6m" v-html="report6m"></span> pelaporan harus dilaporkan</div>
							</div>
							
							<div class="mb-4 card-body border">
								<h5>3 Bulan</h5>
								<div class="content-count"><span id="report-3m" v-html="report3m"></span> pelaporan harus dilaporkan</div>
							</div>
							<div class="mb-4 card-body border">
								<h5>1 Bulan</h5>
								<div class="content-count"><span id="report-1m" v-html="report1m"></span> pelaporan harus dilaporkan</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</Dashboard>
</template>

<script>
	
	import { permitCount } from '@/api/document'
	import { reportingCount } from '@/api/reporting'
	
	export default{
		name: 'Environment',
		data(){
			return{
				isError: false,
				permit12m: '-',
				permit6m: '-',
				permit3m: '-',
				permit1m: '-',
				permit1w: '-',
				report12m: '-',
				report6m: '-',
				report3m: '-',
				report1m: '-',
			}
		},
		async mounted(){
			let now = new Date();
			let endDate = null;
			let statuses = [];
			
			// permit count
			endDate = new Date(new Date().setMonth(new Date().getMonth()+12));
			this.countPermit('permit12m', {
				startDate: now,
				endDate: endDate,
			});
			endDate = new Date(new Date().setMonth(new Date().getMonth()+6));
			this.countPermit('permit6m', {
				startDate: now,
				endDate: endDate,
			});
			endDate = new Date(new Date().setMonth(new Date().getMonth()+3));
			this.countPermit('permit3m', {
				startDate: now,
				endDate: endDate,
			});
			endDate = new Date(new Date().setMonth(new Date().getMonth()+1));
			this.countPermit('permit1m', {
				startDate: now,
				endDate: endDate,
			});
			endDate = new Date(new Date().setDate(new Date().getDate()+7));
			this.countPermit('permit1w', {
				startDate: now,
				endDate: endDate,
			});
			
			// report count
			endDate = new Date(new Date().setMonth(new Date().getMonth()+12));
			this.countReport('report12m', {
				startDate: now,
				endDate: endDate,
			});
			endDate = new Date(new Date().setMonth(new Date().getMonth()+6));
			this.countReport('report6m', {
				startDate: now,
				endDate: endDate,
			});
			endDate = new Date(new Date().setMonth(new Date().getMonth()+3));
			this.countReport('report3m', {
				startDate: now,
				endDate: endDate
			})
			endDate = new Date(new Date().setMonth(new Date().getMonth()+1));
			this.countReport('report1m', {
				startDate: now,
				endDate: endDate,
			});
		},
		methods: {
			async countPermit(key, data){
				this[key] = '<i class="fas fa-spin fa-spinner"></i>';
				try{
					let request = await permitCount(data);
					this[key] = request.data;
				}catch(e){
					this[key] = '-';
					console.log("failed getting " + key + " count");
					console.log(e);
					if(!this.isError && e.response?.status == 401){
						this.isError = true;
						alert("Sesi sudah tidak valid, silahkan login");
						await this.$store.dispatch("auth/authLogout");
						this.$router.replace("/login");
					}
				}
			},
			async countReport(key, data){
				this[key] = '<i class="fas fa-spin fa-spinner"></i>';
				try{
					let request = await reportingCount(data);
					this[key] = request.data;
				}catch(e){
					this[key] = '-';
					console.log("failed getting " + key + " count");
					console.log(e);
					if(!this.isError && e.response?.status == 401){
						this.isError = true;
						alert("Sesi sudah tidak valid, silahkan login");
						await this.$store.dispatch("auth/authLogout");
						this.$router.replace("/login");
					}
				}
			}
		}
	}
</script>

<style type="text/css">
	.content-count{
		font-style: italic;
	}
	.content-count span{
		margin-right: 10px;
		font-style: normal;
		font-size: 25px !important;
		color: #9d7b31;
	}
</style>