import { httpClient,  httpClientForm } from './httpClient';

const END_POINT = '/api/document';

const documentAll = () => {
	return httpClient.get(END_POINT + "/");
}
const documentById = (id) => {
	return httpClient.get(END_POINT + "/" + id);
}
const documentAdd = (data) => {
	return httpClient.post(END_POINT + "/", data);
}
const documentUpdate = (id, data) => {
	return httpClient.put(END_POINT + "/" + id, data);
}
const documentDelete = (id) => {
	return httpClient.delete(END_POINT + "/" + id);
}
const documentLastCode = (codeId) => {
	return httpClient.get(END_POINT + "/latest/code_id/" + codeId);
}
const documentCheckDuplicate = (codeId, documentCode) => {
	return httpClient.get(END_POINT + "/is_duplicate/" + codeId + "/" + documentCode);
}
const documentSearch = (query) => {
	return httpClient.get("document_permit_name/" + query);
}
const documentFilter = (filter) => {
	return httpClient.post("/filter_document", filter);
}
const documentExport = (data) => {
	return httpClient.post("/generate_excel", data);
}
const permitCount = (data) => {
	return httpClient.post('/get_expired_document_count', data);
}

export {
	documentAll,
	documentById,
	documentAdd,
	documentUpdate,
	documentDelete,
	documentLastCode,
	documentCheckDuplicate,
	documentSearch,
	documentFilter,
	documentExport,
	permitCount,
}