import { httpClient,  httpClientFile } from './httpClient';

const END_POINT = '/api/reporting';

const reportingAll = () => {
	return httpClient.get(END_POINT);
}
const reportingByDocument = (documentId) => {
	return httpClient.get("/document_id/" + documentId);
}
const reportingSave = (data) => {
	return httpClientFile.post(END_POINT, data);
}
const reportingUpdate = (data) => {
	return httpClientFile.post("update", data);
}
const reportingDelete = (id) => {
	return httpClient.delete(END_POINT + "/" + id);
}
const reportingDetail = (documentId) => {
	return httpClient.get(END_POINT + "/v2/compulsory_report/detail/" + documentId);
}
const reportingShow = (id) => {
	return httpClient.get("reporting_id/" + id);
}
const uploadReport = (reportingTimeTableId, data) => {
	return httpClientFile.post("/api/reporting/reported/reporting_time_table_id/" + reportingTimeTableId, data);
}
const uploadReceipt = (reportingTimeTableId, data) => {
	return httpClientFile.post("/api/reporting/reporting_receipt/reporting_time_table_id/" + reportingTimeTableId ,data);
}
const reportingCount = (data) => {
	return httpClient.post('/get_expired_reporting_count', data);
}

export {
	reportingByDocument,
	reportingAll,
	reportingSave,
	reportingUpdate,
	reportingDelete,
	reportingShow,
	reportingDetail,
	uploadReport,
	uploadReceipt,
	reportingCount,
}